import axios from "axios"

function makeUrl(path){
    return laravel.domain + path
}

const ngrok = () => {
    let servicio = window.localStorage.getItem("servicio")
    return servicio??  "https://4b57-2001-1388-6660-79e8-58bf-a247-ce26-6d5e.ngrok.io/api"
}

const remote = false;

const axiosInstance = axios.create(
    {
        baseURL: remote? ngrok():'http://127.0.0.1:8000/api'
    }
)

const laravel = {
    domain: remote? ngrok():'http://127.0.0.1:8000/api',
    makeUrl,
    axiosInstance
}

export default laravel

